//
// reboot.scss
//

html {
    position: relative;
    min-height: 100%;
}

body {
    overflow-x: hidden;
    background: linear-gradient(rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.5)),
        url("../images/body-bg-light.jpg") center / cover no-repeat fixed;
    background-size: cover;
    background-position: center;
}

[data-bs-theme="dark"] {
    body {
        background: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.5)),
            url("../images/body-bg-dark.jpg") center / cover no-repeat fixed;
    }
}

::selection {
    color: $primary;
    background: rgba($primary, 0.15);
}

button,
a {
    outline: none !important;
}

// Forms

label {
    font-weight: $font-weight-medium;
}

b,
strong {
    font-weight: $font-weight-semibold;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: $font-family-secondary;
}


// border style
.border-dashed {
    --#{$prefix}border-style: dashed;
}

// Custom Link
.link-reset {
    color: inherit;
    transition: $transition-base;

    &:hover {
        color: var(--#{$prefix}primary);
    }
}


.page-container {
    @extend .container-fluid;
}


// .page-container {
//     max-width: 90%;
//     @extend .container;  
// }


html[data-bs-theme="dark"] {

    .text-bg-dark,
    .text-bg-light,
    .bg-light.text-dark,
    .bg-dark.text-light {
        color: var(--#{$prefix}white) !important;
    }
}